*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Roboto", "Arial", sans-serif;
  color: #102654;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
}

h1,
h2 {
  font-family: "Alumni", "Arial", sans-serif;
}

h1 {
  font-size: 92px;
  font-weight: 800;
  line-height: 110px;
  letter-spacing: 4.6px;
}

h2 {
  font-size: 54px;
  font-weight: 600;
  line-height: 54px;
  letter-spacing: 1.62px;
}

h3 {
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 26px;
  font-weight: 700;
  line-height: 26px;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #102654;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1366px;
  margin: 0 auto;
  padding: 0 103px;
}
@media (max-width: 1365px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}
.container--header {
  background: linear-gradient(90deg, #1c3374 0%, #1c3374 0.01%, #1a3578 39.11%, #73184b 75.5%, #b4022a 100%);
}
.container--hero {
  background: linear-gradient(90deg, #1c3374 0%, #1c3374 0.01%, #1a3578 39.11%, #73184b 75.5%, #b4022a 100%);
}
.container--background-light {
  background-color: #f8f9fc;
}
.container--background-dark {
  background-color: #102654;
}
.container--footer {
  background-color: #030a1a;
}

.container__header {
  display: flex;
  justify-content: space-between;
  padding-top: 30px;
  padding-bottom: 30px;
}

.container__hero {
  padding-bottom: 120px;
}

.container__tickets {
  position: relative;
}

.container__section {
  padding-top: 120px;
  padding-bottom: 120px;
}

.container__form {
  padding-top: 60px;
  padding-right: 170px;
  padding-bottom: 60px;
}

.container__contacts {
  padding: 60px 60px 60px 100px;
  border-top: 1px solid #74819c;
}

.container__half-width-wrapper {
  padding: 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.accordion {
  margin: 0 auto;
}
.accordion .accordion {
  width: 90%;
  margin: 0 auto;
}
.accordion__element:last-child {
  margin-bottom: 0;
}
.accordion__element {
  position: relative;
  padding: 25px 0;
}
.accordion__element::after {
  position: absolute;
  content: "";
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: rgba(28, 51, 116, 0.2);
}
.accordion__element:first-child::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: rgba(28, 51, 116, 0.2);
}
.accordion__element:hover {
  cursor: pointer;
}
.accordion__element.is-active > .accordion__button {
  opacity: 0.5;
}
.accordion__button {
  width: 100%;
  margin-left: auto;
  padding: 10px;
  flex-shrink: 0;
  text-transform: uppercase;
  cursor: pointer;
}
.accordion__button.is-active {
  background-color: #1c3374;
}
.accordion__button.is-active::before {
  background-color: #ffffff;
}
.accordion__button.is-active::after {
  background-color: transparent;
}
.accordion__wrapper {
  padding: 10px;
}
.accordion h3 {
  margin: 0;
  margin-bottom: 4px;
}
.accordion p {
  width: 660px;
  margin: 0;
  margin-bottom: 5px;
}

.accordion__title-wrapper {
  display: flex;
  align-items: center;
}

[data-accordion=element].is-active > [data-accordion=content] {
  max-height: 100%;
}

[data-accordion=parent].no-transition [data-accordion=content] {
  transition: none;
}

[data-accordion=content] {
  max-height: 0;
  transition: max-height 0.3s;
  overflow: hidden;
}

[data-tabs=parent] {
  overflow: hidden;
}
[data-tabs=parent].no-transition > [data-tabs=control],
[data-tabs=parent].no-transition > [data-tabs=content],
[data-tabs=parent].no-transition > [data-tabs=element] {
  transition: none;
}
[data-tabs=parent].no-transition-global [data-tabs=control],
[data-tabs=parent].no-transition-global [data-tabs=content],
[data-tabs=parent].no-transition-global [data-tabs=element] {
  transition: none;
}

[data-tabs=parent] [data-tabs=content] {
  position: relative;
}

[data-tabs=element] {
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s ease-in, visibility 0.3s ease-in;
}

[data-tabs=element].for-load {
  position: static;
  visibility: visible;
  opacity: 1;
  transition: none;
}

[data-tabs=element].is-active {
  z-index: 1;
  visibility: visible;
  opacity: 1;
}

[data-accordion-init] > [data-tabs=content],
[data-accordion-init] > [data-tabs=controls] {
  display: none;
}
[data-accordion-init] [data-tabs=element] {
  z-index: 1;
  visibility: visible;
  position: static;
  opacity: 1;
}

[data-tabs=accordion-wrapper] {
  display: block;
  max-height: 0;
  transition: max-height 0.3s ease;
  overflow: hidden;
}

[data-tabs=accordion-content] {
  overflow: hidden;
}

[data-tabs=accordion] {
  overflow: hidden;
  position: relative;
  isolation: isolate;
}

[data-tabs=accordion].is-active > [data-tabs=accordion-wrapper] {
  max-height: none;
}

.tabs {
  max-width: 100%;
}
@media screen and (max-width: 767px) {
  .tabs [data-tabs=accordion] {
    margin: 10px 2% 0;
  }
}
.tabs [data-tabs=accordion] > .tabs__control.is-active {
  pointer-events: auto;
  opacity: 0.5;
}
@media screen and (max-width: 767px) {
  .tabs [data-tabs=accordion] > .tabs__control {
    width: 100%;
    margin: 0;
    padding: 10px;
  }
}
.tabs [data-tabs=accordion].is-active > .tabs__control {
  opacity: 1;
}
.tabs__controls {
  justify-content: space-between;
  width: 100%;
}
@media screen and (max-width: 767px) {
  .tabs__controls {
    flex-wrap: wrap;
  }
}
.tabs__control {
  text-transform: uppercase;
  cursor: pointer;
  min-width: auto;
  justify-content: center;
  width: 100%;
  margin-bottom: 10px;
}
@media screen and (max-width: 1023px) {
  .tabs__control {
    margin: 0 10px;
    padding: 15px;
  }
}
@media screen and (max-width: 767px) {
  .tabs__control {
    margin: 10px 2% 0;
    padding: 10px;
    width: 45%;
  }
}
.tabs__control.is-active {
  pointer-events: none;
  color: #ffffff;
  background: linear-gradient(97deg, #ed0233 17.6%, #003eb7 95.31%);
  border: none;
}
.tabs__content {
  transition: height 0.3s;
}
.tabs__element {
  position: absolute;
  width: 100%;
}
@media screen and (max-width: 767px) {
  .tabs__element {
    padding: 15px;
  }
}
.tabs--unset .tabs__content {
  height: 100px;
}
.tabs--unset .tabs__element {
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
}

