  flex-wrap: wrap;
  color: #ffffff;
}

.footer__social-list {
  display: flex;
  flex-wrap: nowrap;
  gap: 10px;
}

.footer__link {
  position: relative;
  padding: 5px 10px;
  color: #ffffff;
}
.footer__link:hover::after {
  position: absolute;
  content: "";
  left: 10px;
  bottom: -5px;
  width: calc(100% - 20px);
  height: 1px;
  background-color: #ffffff;
}
.footer__link:focus {
  padding: 5px 10px;
  border: 1px solid #ffffff;
  outline: none;
}
.footer__link:active::after {
  display: none;
}
.footer__link--disabled {
  opacity: 0.4;
  pointer-events: none;
}

